.inputWrapper{
	margin: 10px 5px;
	display: flex;
	flex-direction: row;
	overflow: hidden;
}

.inputWrapper *{
	margin: 0;
	padding: 0;
	outline: none;
	border: none;
}

.inputStyles{
  line-height: 1.5em;
  border: 1px solid #000;
  padding: 8px 20px;
  font-size: 30px;
  text-align: center;
  background: #b2dfdb;
}

.inputWrapper .inputStyles{
  width: 100px;
  border-right: none;
	border-left: none;
}

.inputWrapper .changingValue{
	display: block;
	font-size: 40px;
	width: 70px;
	height: 70px;
	border: 1px solid #000;
	background: #43a047;
	cursor: pointer;
	transition: background ease .5s, box-shadow ease 0.1s;
	z-index: 2;
	box-shadow: inset 10px 0px 17px -23px rgba(0,0,0,0.75);
}

.inputWrapper .changingValue:hover{
	background: #4caf50;
}

.inputWrapper .changingValue:first-child{
	border-right: none;
	border-top-left-radius: 100%;
	border-bottom-left-radius: 100%;
}

.inputWrapper .changingValue:first-child:active{
	box-shadow: inset 10px 0px 17px -12px rgba(0,0,0,0.75);
}

.inputWrapper .changingValue:last-child{
	border-left: none;
	border-top-right-radius: 100%;
	border-bottom-right-radius: 100%;
}

.inputWrapper .changingValue:last-child:active{
	box-shadow: inset -10px 0px 17px -12px rgba(0,0,0,0.75);
}

.inputWrapper .changingValue.disable{
	cursor: not-allowed;
	background: #005005;
	color: #fff;
}

textarea.inputStyles{
	resize: none;
	max-width: 400px;
	min-width: 200px;
	width: 100%;
}
.divsStyles{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.divsStyles .title{
	font-size: 30px;
	margin: 30px 0;
}

.divsStyles .passButton,
.divsStyles .buttonClipboard{
	position: relative;
	transition: all ease .5s;
	margin: 10px 0;
	padding: 10px 20px;
	border: 2px solid #43a047;
	background: #66bb6a;
	cursor: pointer;
	font-size: 20px;
}

.divsStyles .passButton{
	border-radius: 34% / 100%;
}

.divsStyles .buttonClipboard{
	border-radius: 26% / 100%;
}

.divsStyles .passButton:hover,
.divsStyles .buttonClipboard:hover{
	background: #43a047;
}

.divsStyles .buttonClipboard .tooltip{
	opacity: 0;
	position: absolute;
	bottom: 140%;
	left: 0%;
	width: 100%;
	padding: 10px 20px;
	background: #66bb6a;
	border-radius: 38% / 100%;
	border: 2px solid #43a047;
	z-index: 4;
	transition: all ease .5s;
}

.divsStyles .buttonClipboard .tooltip::after{
	content: '';
	position: absolute;
	bottom: calc(-10px - 2*2px);
	left: calc(50% - 10px);
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-top: 14px solid #43a047;
}

.wraplabel{
	margin: 4px 0;
}

.wraplabel .infoStyles{
  text-decoration: none;
  color: #000;
  cursor: pointer;

  font-size: 30px;

	background-image: linear-gradient(to bottom,#1f133a 0,#1f133a 100%);
	background-size: 0px 3px;
	background-position: 0% 100%;
	background-repeat: no-repeat;
	transition: all ease .5s;
}

.wraplabel .infoStyles:hover{
	background-size: 100% 3px;
}
*{
	margin: 0;
	padding: 0;
	outline: none;
	border: none;
	box-sizing: border-box;
	font-family: "Proxima Nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.App{
	display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;

  min-height: 100vh;
  min-width: 100vw;
  background: #c5e1a5;
}
